.footer{
    background-color: #f2f2f2;
}


.footer__inner{
    width: 280px;
    padding: 39px 0 22px 0;
    margin: 0 auto;
}
@media(min-width:768px){
    .footer__inner{
        width: 680px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 58px 0;
    }
}
@media(min-width:1440px){
    .footer__inner{
        width: 1220px;
    }
}


.footer__logo-link{
    display: block;
    padding-bottom: 31px;
}
@media(min-width:768px){
    .footer__logo-link{
        padding-bottom: 0px;
    }
}


.footer__logo-img{
    width: 128px;
    height: 23px;
    display: block;
    margin: 0 auto;
}
.footer__logo-img:hover{
    opacity: 0.8;
}
.footer__logo-img:active{
    opacity: 0.6;
}


.footer__social{
    padding: 22px 0 22px 0;
    border-top: 1px solid #dadada;
    border-bottom: 1px solid #dadada;
    margin-bottom: 22px;
}
@media(min-width:768px){
    .footer__social{
        padding: 0px 0 0px 0;
        border-top:none;
        border-bottom: none;
        margin-bottom: 0;
    } 
}


.footer__social-list{
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer__social-item:not(:last-child) {
    margin-right: 37px;
}
@media(min-width:768px){
    .footer__social-item:not(:last-child) {
        margin-right: 28px;
    }
}


.footer__social-img{
    fill: #666666;
}
.footer__social-img:hover{
    fill: #68b738;
}
.footer__social-img:active{
    fill:#68b738;
    opacity: 0.3;
}


// vk
.footer__social-img--vk{
    width: 22px;
    height: 12px;
}
@media(min-width:768px){
    .footer__social-img--vk{
        width: 30px;
        height: 16px;
    }
}

//insta
.footer__social-img--insta{
    width: 16px;
    height: 16px;
}
@media(min-width:768px){
    .footer__social-img--insta{
        width: 23px;
        height: 23px;
    }
}

//fb
.footer__social-img--fb{
    width: 10px;
    height: 19px;
}
@media(min-width:768px){
    .footer__social-img--fb{
        width: 14px;
        height: 28px;
    }
}


.footer__html-academy{
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.footer__html-academy-link{
    color: #444444;
    font-size: 16px;
    line-height: 16px;
    padding-right: 23px;
}
.footer__html-academy-img{
    width: 27px;
    height: 34px;
    fill: #666666;
}

.footer__html-academy:hover >.footer__html-academy-img{
    fill:#68b738;
}


.footer__html-academy:active >.footer__html-academy-link{
    opacity: 0.3;
}
.footer__html-academy:active >.footer__html-academy-img{
    opacity: 0.3;
    fill:#68b738;

}


