.map__inner{
    position: relative;
}


.map__discr{
    // border:1px solid black;
    width: 320px;
    padding: 32px 25px 26px 19px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
@media(min-width: 768px){
    .map__discr{
        width: 768px;
        padding: 66px 90px 59px 76px;
    } 
}
@media(min-width: 1440px){
    .map__discr{
        width: 570px;
        padding: 66px 72px 59px 71px;
        background-color: #ffffff;
        position: absolute;
        top: 95px;
        left: 110px;
    } 
}



.map__invite{
    @extend %oswald-400;
    font-size: 16px;
    line-height: 20px;
    text-align: left;
    color: #111111;
    text-transform: uppercase;
    width: 123px;
}
@media(min-width: 768px){
    .map__invite{
        font-size: 20px;
        line-height: 25px;
        width: 155px;
    }
}


.map__address{
    font-size: 14px;
    line-height: 20px;
    width: 135px;
    text-align: left;
}
@media(min-width: 768px){
    .map__address{
        font-size: 16px;
        width: 155px;
        text-align: right;
    }
}

@media(min-width: 768px){
    .map__address--city{
        display: block;
        margin-top: 1em;
    }
}

// Карта

.map__card{
    width: 100%;
    height: 363px;
}
@media(min-width: 768px){
    .map__card{
        height: 400px;
    }
}



